#landingpage {
    text-align: center;

    display: flex;
    justify-content: center;
    align-items: center;

    width: 100%;
    height: 100vh;

    /* border-bottom: 2px solid #687789; */

    margin-bottom: 100px;
}

#landingpage h1 {
    color: #FF6A67;
    font-size: 50px;
    font-weight: 700;

    margin: 0px;
    padding: 0px;

    line-height: 60px;
}

#landingpage p {
    color: white;
    font-size: 30px;
    font-weight: 700;

    max-width: 60vw;

    margin: 0px;
    margin-top: 50px;
}



