.infoslide {

    width: 100%;
    height: fit-content;

    padding: 0px calc(125px + (100vw / 139.6) + 5px);

    margin-bottom: 100px;
}
.infoslide:last-child {
    margin-bottom: 200px;
}

.infoslide .iscont {
    width: 100%;
    height: fit-content;

    /* border-left: 10px solid #687789;
    border-right: 10px solid #FF6A67; */

    padding: 30px 50px;
    padding-bottom: 40px;
}
.infoslide .iscont.left {
    border-right: 10px solid #687789;
    border-left: 10px solid #FF6A67;
}
.infoslide .iscont.right {
    border-left: 10px solid #687789;
    border-right: 10px solid #FF6A67;
}

.infoslide .iscont .isheader {
    width: 100%;
    height: fit-content;

    margin-bottom: 30px;
}
.infoslide .iscont .isheader label {
    color: #FF6A67;
    font-size: 35px;
    font-weight: 700;
}

.infoslide label {
    display: block;
}

.infoslide .iscont .isdata {
    color: white;
    font-size: 20px;
    font-weight: 600;
}

.infoslide .iscont .issubheader {
    color: #7B8898;
    font-size: 20px;
    font-weight: 700;

    margin-top: 30px;
}
.infoslide .iscont .issubdata {
    color: white;
    font-size: 17px;
    font-weight: 600;

    margin-top: 15px;
}


.infoslide .iscont .istop {
    display: flex;
    justify-content: space-between;
    align-items: center;

    width: 100%;
    height: fit-content;
}
.infoslide .iscont .istop .istleft {
    width: 60%;
}
.infoslide .iscont .istop .istleft label {

}
.infoslide .iscont .istop .istright {
    display: flex;
    justify-content: center;
    align-items: center;

    width: 50%;
    height: fit-content;
}
.infoslide .iscont .istop .istright img {
    min-height: 200px;
}

.infoslide .iscont .isbottom {
    display: flex;
    justify-content: space-evenly;
    /* align-items: center; */

    width: 100%;
    height: fit-content;
}
.infoslide .iscont .isbottom .isbleft {
    width: 100%;
    padding-right: 50px;
}
.infoslide .iscont .isbottom .isbright {
    width: 100%;
    padding-left: 50px;
}

.infoslide .iscont .isfact {
    width: 100%;
    height: fit-content;
}
