#app {
  position: relative;

  width: 100%;
  height: fit-content;

  background-size: contain;
}


#bg {
  z-index: -1;

  display: block;

  position: absolute;
  top: 0px;
  left: 50%;
  transform: translate(-50%, 0px);

  width: calc(100vw -  260px);
  height: auto;
}

.pointheadercont {
  width: 100%;
  height: fit-content;

  padding: 0px calc(125px + 80px);

  margin-bottom: 150px;
}
.pointheadercont label {
  display: block;
}
.pointheadercont .pheader {
  color: #FF6A67;
  font-size: 45px;
  font-weight: 700;
}
.pointheadercont .psubheader {
  color: #687789;
  font-size: 35px;
  font-weight: 700;

  margin-top: 30px;
}
.pointheadercont .pdata {
  color: white;
  font-size: 25px;
  font-weight: 600;

  margin-top: 15px;
}


.divider {
  width: 100%;
  height: 35px;

  background-color: #373F48;
}

#stats {
  display: flex;
  justify-content: space-evenly;
  align-items: center;

  width: 100%;
  height: 100px;

  background-color: #373F48;
  border-bottom: 1px solid #687789;

  padding: 0px 30px;
}
#stats .stat {
  display: flex;
  justify-content: center;
  align-items: center;

  width: 100%;
  height: 100%;
}
#stats .stat .stattitle {
  color: white;
  font-size: 16px;
  font-weight: 600;
}
#stats .stat .statvalue {
  color: #FF6A67;
  font-size: 16px;
  font-weight: 600;
}
#stats .stat .statdivider {
  display: block;

  color: #687789;
  font-size: 16px;
  font-weight: 600;

  margin: 0px 5px;
}


