#game {
    position: relative;

    /* display: flex;
    justify-content: center;
    align-items: center; */

    width: 100%;
    height: 100vh;

    background-color: #7FF180;

    background-repeat: no-repeat;
    background-size: cover;
    background-position: center;
}

#game #grass {
    position: absolute;
    top: 50%;
    left: 50%;
    transform: translate(-50%, -50%);

    max-width: 80vw;
}

#game #genbtn {
    cursor: pointer;

    z-index: 200;

    position: absolute;
    top: 15px;
    left: 10px;

    width: fit-content;
    height: 60px;

    color: white;
    font-size: 17px;
    font-weight: 700;

    background-color: #FF6A67;
    border: 3px solid black;
    border-radius: 5px;

    padding: 0px 20px;
    padding-bottom: 3px;

    background-image: repeating-linear-gradient(
        -55deg,
        #FF7D7A,
        #FF7D7A 20px,
        #FF6A67 20px,
        #FF6A67 40px
    );
    
    transition: filter .1s;
}
#game #genbtn:hover {
    filter: brightness(.9);
}

#game #toolbar {
    z-index: 200;

    position: absolute;
    top: 15px;
    right: 10px;

    display: flex;

    width: fit-content;
    height: 60px;

    background-color: #373F48;
    border: 3px solid black;
    border-radius: 5px;
}
#game #toolbar .tboption {
    cursor: pointer;

    display: flex;
    justify-content: center;
    align-items: center;

    width: 60px;
    height: 100%;

    border-right: 2px solid #687789;

    transition: background-color .1s;
}
#game #toolbar .tboption:hover {
    background-color: #2C3238;
}
#game #toolbar .tboption:last-child {
    border: none;
}
#game #toolbar .tboption img {
    max-height: 50%;
}

.blob {
    position: absolute;

    width: 50px;
    max-height: 100px;

    transform: scale(.1);
    /* background: rgb(231,0,0);
    background: linear-gradient(90deg, rgba(231,0,0,1) 0%, rgba(3,0,255,1) 100%); */

    /* transform: scale(.5); */

    /* background-color: rgb(82, 199, 82);

    border: 2px solid black; */
    
    /* transition: transform .5s; */
}



.blob.dead {
    display: none;
}

.blob.moving {
    animation: moving 1s infinite;
    animation-timing-function: linear;
}
@keyframes moving {
    25% {
        transform: rotate(10deg);
    }
    75% {
        transform: rotate(-10deg);
    }
}



